import { Component, OnInit } from '@angular/core';
import { Service } from './service';

@Component({
  selector: 'geno-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss']
})
export class ServicesComponent implements OnInit {
  services: Array<Service> = [
    new Service({ title: 'لوکس', icon: 'lux', about: 'سرویس شماره یک. شیک، سریع و پرهزینه' }),
    new Service({ title: 'خانواده', icon: 'suv', about: 'برای وقتهایی  که به فضای بیشتری احتیاج دارید\n تا ظرفیت 6 نفر' }),
    new Service({ title: 'سبز', icon: 'green', about: 'اگر به محیط زیست احمیت میدین این سرویس مناسب شماست. سرویس های سبز با استفاده از ماشین های هیبریدی برای شهری پاکتر.' }),
    new Service({ title: 'ویژه', icon: 'normal', about: 'پیشنهاد ما برای مسافرت های داخل شهری و رفت و آمد های روزانه، سریع، امن و اقتصادی.' }),
    new Service({ title: 'تاکسی', icon: 'normal', about: 'تاکسی های ناوگان تاکسی رانی شهر، بدون تخفیف ' }),
  ];

  constructor() { }

  ngOnInit() {
  }

}



